import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  OnInit,
} from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { fadeInUp400ms } from '../../../../@vex/animations/fade-in-up.animation';
import { NgZone, AfterViewInit } from '@angular/core';
import icLogout from '@iconify/icons-fa-solid/sign-out-alt';
import { Title } from '@angular/platform-browser';
import { WhareService } from 'src/app/services/wharehouse/wharehouse.service';
import { Warehouse } from 'src/app/interfaces/wharehouse/wharehouse';

@Component({
  selector: 'app-client-selector',
  templateUrl: './client-selector.component.html',
  styleUrls: ['./client-selector.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [fadeInUp400ms],
})
export class ClientSelectorComponent implements OnInit, AfterViewInit {
  inLoad = false;
  selectedWhareHouse?: Warehouse = null;
  name: string;
  direction: string;

  visible = false;
  icLogout = icLogout;

  listClients: Warehouse[] = [];
  visibleForm = false;

  constructor(
    private router: Router,
    private cd: ChangeDetectorRef,
    private snackbar: MatSnackBar,
    private zone: NgZone,
    private route: ActivatedRoute,
    private titleService: Title,
    private whareHouseService: WhareService
  ) {
    this.inLoad = false;
    this.whareHouseService.selectedWharehouse$.subscribe((response: any) => {
      this.selectedWhareHouse = response;
    });

    this.getData();
  }

  getData() {
    this.whareHouseService.getWharehouse().subscribe((response: any) => {
      this.listClients = response;
      this.inLoad = false;

      return;
    });
  }

  ngOnInit(): void {
    this.titleService.setTitle('Select Wharehouse');
  }

  ngAfterViewInit() {}

  send() {
    this.snackbar.open('Wharehose Selected');
    this.whareHouseService.selectWhareHouse(this.selectedWhareHouse);
    this.router.navigate(['/']);
  }

  showForm() {
    if (this.visibleForm === false) {
      this.visibleForm = true;
    } else {
      this.createWharehouse();
    }
  }

  createWharehouse() {
    this.visibleForm = false;
    this.whareHouseService
      .createWharehouse(this.name, this.direction)
      .subscribe((response: any) => {
        this.getData();
      });
  }

  public objectComparisonFunction = function (option, value): boolean {
    return option.id === value?.id;
  };

  logout() {}
}
